@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
html {
	scroll-behavior: smooth;
}

body {
	height: 100%;
	background: linear-gradient(to right, #485563, #29323c);
}
.main-page {
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;

    font-family: 'Roboto', sans-serif;
}

.main-item {
    /* width: 70%; */
    /* align-items: left; */
    /* margin-bottom: 20px;
    margin-top: 20px; */
}
.header-component {
    position: absolute;
    width: 100%;
    font-family: 'Roboto', sans-serif;
}

.header-component .header-title {
    color: white;
}

.ant-page-header {
    background: none;
    background-color: transparent;
    z-index: 10;
}

.header-icon {
    height: 30px;
    width: 30px;
}

.header-icon:focus {
    outline: none;
}
.subheader {
    width: 100%;
    display: flex;
    align-items: center;
    align-self: center;
    flex-direction: column;
    justify-content: center;

    font-family: 'Roboto', sans-serif;

    /* background-image: url("../../Assets/background.jpg"); */
    /* background-size: 300vh;
    background-repeat: no-repeat; */
    /* opacity: 0.2; */

    height: 100vh;
    min-width: 100vw;
}

.subheader h1 {
    font-size: 15vw;
    color: white;
}

.subheader p {
    font-size: 1.2rem;
    color: white;
}

@media only screen and (min-width: 768px) {

    .subheader {
        /* background-image: none;
        background-color: grey; */
    }

    .subheader h1 {
        font-size: 10vw;
    }

    .subheader p {
        font-size: 3vw;
    }
}
.contact-page {
	width: 100%;
	background-color: white;
	font-family: 'Roboto', sans-serif;
	padding-bottom: 40px;
}

.contact-page .contact-container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width: 75%;
	margin: auto;

	border: 1px solid rgb(218, 218, 218);
	box-shadow: 1px 1px 10px rgb(216, 214, 214);
	padding: 20px 0;
}

.contact-page .contact-container .title {
	width: 100%;
	color: black;
	text-align: center;
}

.contact-page .contact-container form div {
	width: 100%;
}

@media only screen and (min-width: 768px) {

    .contact-page .contact-container {
        max-width: 600px;
    }
}
.footer {
    /* margin-top: 40px; */
    border-top: 1px solid #666;
    background: rgba(0, 0, 0, .5);
    color: white;

    position: relative;
    width: 100%;
    bottom: 0;
    /* height: 10rem; */
    font-family: 'Roboto', sans-serif;
}

.footer-text {
    color: white;
}

.portifolio-content-root {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    padding-top: 15px;

    width: 100%;
}

.portifolio-content-root.web-apps {
    background-color: turquoise;
}

.portifolio-content-root.web-sites {
    background-color: rgb(35, 90, 209);
}

.portifolio-content-root.devops {
    background-color: rgb(64, 224, 136);
}

.portifolio-content-root .title-root {
    display: flex;
    flex-direction: column;
    align-items: left;

    width: 90%;
}

.portifolio-content-root h2, p {
    color: white;
    text-align: left;
}

.portifolio-content-root .title-root h2 {
    font-size: 1.5rem;
}

.portifolio-content-root .ant-carousel {
    /* height: 150px; */
    width: 100%;
}

.portifolio-content-root .ant-carousel .slick-track {
    height: 150px;
}

